
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { commodityListApi } from "./conf/api";
export default {
name: "commodityList",
components: { Scroller, LoadMore },
data() {
return {
title: "",
pageIndex: 1, // 加载数据的页数
categoryId: 0, // 类别
commodityList: [], // 商品列表
loadingShow: false, // 为true显示底部加载布局,为false不显示
loadingEnd: true 
};
},

methods: {

onCommodityItemClick(item) {
this.$router.push({
name: "commoditydec",
params: {
item: item
}
});
},

onScrolldown() {
this.pageIndex = 1;
this.requestUGetGoodsH5();
},

onScrollup() {
this.pageIndex = this.pageIndex + 1;
this.requestUGetGoodsH5();
},

requestUGetGoodsH5() {
this.$request
.loading(
this,
commodityListApi.UGetGoodsH5,
"post",
{
PromoterId: "",
CategoryId: this.categoryId,
Title: this.title,
PageSize: 20,
PageIndex: this.pageIndex,
platform: 2,
IsTmall: false,
HasCoupon: true,
NeedFreeShipment: false,
StartPrice: 0
},
""
)
.then(
success => {
this.loadingShow = true;
this.loadingEnd = success.data.code == 0 ? true : false;
if (success.data.code == 0) {
if (this.pageIndex == 1) {

this.commodityList = [];
}

this.commodityList = this.commodityList.concat(success.data.data);
}

if (this.pageIndex == 1) this.$refs.scroller.reset({ top: 0 });

this.$refs.scroller.donePullup();
this.$refs.scroller.donePulldown();
},
fial => {

this.$refs.scroller.donePullup();
this.$refs.scroller.donePulldown();
}
);
},

updateData(item) {

this.pageIndex = 1;
this.categoryId = item.Id;
this.title = item.CategoryName;

this.$vux.loading.show({ text: "加载中" });

this.commodityList = [];

this.requestUGetGoodsH5();
}
},

beforeRouteEnter(to, from, next) {
next(vm => {
if (from.name == "index") {

sessionStorage.setItem(
"saveFromParams",
JSON.stringify(to.params.item)
);
var item = to.params.item;

vm.updateData(item);
} else if (from.name == null) {

var item = JSON.parse(sessionStorage.getItem("saveFromParams"));

vm.updateData(item);
} else if (from.name == "commoditydec") {
var refresh = sessionStorage.getItem("refresh");
if (refresh == "true") {

var item = JSON.parse(sessionStorage.getItem("saveFromParams"));

vm.updateData(item);
}
}
});
}
};



